.light-box {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  // gray-dark #43484e in hex.
  --faded-gray-dark: 67, 72, 78;
  background-color: rgba(var(--faded-gray-dark), 0.75);
  // TODO: The above can be replaced with the below when Firefox supports "Relative RGB colors".
  //background-color: rgb(from var(--gray-dark) / 0.75);
}

.loading-box {
  max-width: 480px;
  padding: @grid_gutter-width;
  border: 1px solid var(--gray-dark);
  border-radius: 4px;
  overflow: auto;

  background: var(--gray-5);
  outline: none;

  span.cf-icon-svg {
    padding-right: @gutter-narrow;
  }
}

